export function useShop() {
  const server = inject('server');
  const allShops = ref([]);
  const ShopItem = ref();
  var Shopid = "";
  const size = 100;
  let page = 0;
  //
  const getAllShop = async () => {
    // @ts-ignore
    const res = await server.userApi.geAllShop({
      "page": page,
      "size": size
    });
    if (res.code === 200) {
      allShops.value = res.data;
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
  };
  //
  const ShopApprove = async (id, state) => {
    const formData = {
      id: id,
      state: state
    };
    console.log("-------------ID:", id);
    // @ts-ignore
    const res = await server.userApi.checkShop(formData);
    if (res.code === 200) {
      ElMessage.success("");
      getAllShop();
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
    // console.log(res)
  };
  //
  const deleteShops = async id => {
    // // 1.
    // await ElMessageBox.confirm("", "", {
    //     confirmButtonText: "",
    //     cancelButtonText: "",
    //     type: 'warning'  
    // }).catch(() => {
    //     ElMessage.info("")
    //     return new Promise(() => { })
    // })
    // 2.
    // @ts-ignore
    const res = await server.userApi.deleteShop({
      "id": id
    });
    if (res.code === 200) {
      ElMessage.success("");
      getAllShop();
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
  };
  return {
    getAllShop,
    allShops,
    ShopApprove,
    ShopItem,
    deleteShops
  };
}