export function useCommunity() {
  const server = inject('server');
  const allCommunitys = ref([]);
  const CommunityItem = ref();
  var Communityid = "";
  const size = 100;
  let page = 0;
  //
  const getAllCommunity = async () => {
    // @ts-ignore
    const res = await server.userApi.geAllCommunitys({
      "page": page,
      "size": size
    });
    if (res.code === 200) {
      allCommunitys.value = res.data;
      // for (const Community of allCommunitys.value) {
      //     console.log("Auth Apply Item Details:");
      //     console.log("ID:", Community.ID);
      //     console.log("State:", Community.state);
      //     console.log("Community:", Community.community);
      //     console.log("Room:", Community.room);
      //     console.log("User ID:", Community.user_id);
      //     console.log("Telephone:", Community.telephone);
      //     console.log("Image URL:", Community.img_url);
      //     console.log("Created At:", Community.CreatedAt);
      //     console.log("--------------------------");
      // }
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
  };
  //
  const CommunityApprove = async id => {
    const formData = {
      id: id,
      state: 1
    };
    console.log("-------------ID:", id);
    // @ts-ignore
    const res = await server.userApi.checkCommunitys(formData);
    if (res.code === 200) {
      ElMessage.success("");
      getAllCommunity();
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
    // console.log(res)
  };
  //
  const CommunityReject = async id => {
    const formData = {
      'id': id,
      'state': 2
    };
    // @ts-ignore
    const res = await server.userApi.checkCommunitys(formData);
    if (res.code === 200) {
      ElMessage.success("");
      getAllCommunity();
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
    // console.log(res)
  };
  //
  const deleteCommunitys = async id => {
    // // 1.
    // await ElMessageBox.confirm("", "", {
    //     confirmButtonText: "",
    //     cancelButtonText: "",
    //     type: 'warning'  
    // }).catch(() => {
    //     ElMessage.info("")
    //     return new Promise(() => { })
    // })
    // 2.
    // @ts-ignore
    const res = await server.userApi.deleteCommunitys({
      "id": id
    });
    if (res.code === 200) {
      ElMessage.success("");
      getAllCommunity();
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
  };
  return {
    getAllCommunity,
    allCommunitys,
    CommunityApprove,
    CommunityReject,
    CommunityItem,
    deleteCommunitys
  };
}