export function useAuthApply() {
  const server = inject('server');
  const allAuthApplys = ref([]);
  const AuthApplyItem = ref();
  var AuthApplyid = "";
  //
  const getAllAuthApply = async () => {
    // @ts-ignore
    const res = await server.userApi.geAllAuthApplys();
    if (res.code === 200) {
      allAuthApplys.value = res.data;
      // for (const authApply of allAuthApplys.value) {
      //     console.log("Auth Apply Item Details:");
      //     console.log("ID:", authApply.ID);
      //     console.log("State:", authApply.state);
      //     console.log("Community:", authApply.community);
      //     console.log("Room:", authApply.room);
      //     console.log("User ID:", authApply.user_id);
      //     console.log("Telephone:", authApply.telephone);
      //     console.log("Image URL:", authApply.img_url);
      //     console.log("Created At:", authApply.CreatedAt);
      //     console.log("--------------------------");
      // }
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
  };
  //
  const authApplyApprove = async id => {
    const formData = {
      id: id,
      state: 2
    };
    console.log("-------------ID:", id);
    // @ts-ignore
    const res = await server.userApi.checkAuthApplys(formData);
    if (res.code === 200) {
      ElMessage.success("");
      getAllAuthApply();
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
    // console.log(res)
  };
  //
  const authApplyReject = async id => {
    const formData = {
      'id': id,
      'state': 3
    };
    // @ts-ignore
    const res = await server.userApi.checkAuthApplys(formData);
    if (res.code === 200) {
      ElMessage.success("");
      getAllAuthApply();
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
    // console.log(res)
  };
  //
  const deleteAuthApply = async id => {
    // // 1.
    // await ElMessageBox.confirm("", "", {
    //     confirmButtonText: "",
    //     cancelButtonText: "",
    //     type: 'warning'
    // }).catch(() => {
    //     ElMessage.info("")
    //     return new Promise(() => { })
    // })
    // 2.
    // @ts-ignore
    const res = await server.userApi.deleteAuthApplys({
      "id": id
    });
    if (res.code === 200) {
      ElMessage.success("");
      getAllAuthApply();
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
  };
  return {
    getAllAuthApply,
    allAuthApplys,
    authApplyApprove,
    authApplyReject,
    AuthApplyItem,
    deleteAuthApply
  };
}