export function useConfig() {
  const server = inject('server');
  const allConfigs = ref([]);
  const ConfigItem = ref();
  var Configid = "";
  const size = 100;
  let page = 0;
  //
  const getAllConfig = async () => {
    // @ts-ignore
    const res = await server.userApi.geAllConfig({
      "page": page,
      "size": size
    });
    if (res.code === 200) {
      allConfigs.value = res.data;
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
  };
  //
  const ConfigApprove = async (id, state) => {
    const formData = {
      id: id,
      state: state
    };
    console.log("-------------ID:", id);
    // @ts-ignore
    const res = await server.userApi.checkConfig(formData);
    if (res.code === 200) {
      ElMessage.success("");
      getAllConfig();
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
    // console.log(res)
  };
  //
  const deleteConfigs = async id => {
    // // 1.
    // await ElMessageBox.confirm("", "", {
    //     confirmButtonText: "",
    //     cancelButtonText: "",
    //     type: 'warning'  
    // }).catch(() => {
    //     ElMessage.info("")
    //     return new Promise(() => { })
    // })
    // 2.
    // @ts-ignore
    const res = await server.userApi.deleteConfig({
      "id": id
    });
    if (res.code === 200) {
      ElMessage.success("");
      getAllConfig();
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
  };
  return {
    getAllConfig,
    allConfigs,
    ConfigApprove,
    ConfigItem,
    deleteConfigs
  };
}