import { ElMessageBox } from "element-plus";
export function useCourse() {
  const server = inject('server');
  const allCates = ref([]);
  const allArticle = ref([]);
  const cateItem = ref();
  const articleItem = ref();
  var cateid = "";
  //
  const getCateAllArticles = async id => {
    cateid = id;
    // @ts-ignore
    const res = await server.userApi.getCateArticles({
      "id": parseInt(id, 10)
    });
    if (res.code === 200) {
      allArticle.value = res.data;
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
    // console.log(res)
  };
  //
  const getCateDetail = async id => {
    // @ts-ignore
    const res = await server.userApi.getCategoryDetail({
      "id": parseInt(id, 10)
    });
    if (res.code === 200) {
      cateItem.value = res.data;
    } else {
      ElMessage.error(res.msg);
      throw new Error(res.msg || '');
    }
    // console.log(res)
  };
  //
  const getAllCate = async () => {
    // @ts-ignore
    const res = await server.userApi.geAllCategorys();
    if (res.code === 200) {
      allCates.value = res.data;
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
    // console.log(res)
  };
  //
  const deleteCate = async id => {
    // 1.
    await ElMessageBox.confirm("", "", {
      confirmButtonText: "",
      cancelButtonText: "",
      type: 'warning'
    }).catch(() => {
      ElMessage.info("");
      return new Promise(() => {});
    });
    // 2.
    // @ts-ignore
    const res = await server.userApi.postDetCategory({
      "id": id
    });
    if (res.code === 200) {
      ElMessage.success("");
      getAllCate();
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
  };
  //
  const deleteArticle = async id => {
    // 1.
    await ElMessageBox.confirm("", "", {
      confirmButtonText: "",
      cancelButtonText: "",
      type: 'warning'
    }).catch(() => {
      ElMessage.info("");
      return new Promise(() => {});
    });
    // 2.
    // @ts-ignore
    const res = await server.userApi.postDetArticle({
      "id": id
    });
    if (res.code === 200) {
      ElMessage.success("");
      getCateAllArticles(cateid);
    } else {
      ElMessage.error('');
      throw new Error(res.msg || '');
    }
  };
  return {
    getAllCate,
    allCates,
    deleteCate,
    getCateDetail,
    cateItem,
    getCateAllArticles,
    allArticle,
    deleteArticle
  };
}